'use client'

import { useState } from 'react'
import Link from 'next/link'
import { PageHeader } from '@/components/ui/PageHeader'

const REPORTING_TABS = [
  { id: 'overview', label: 'Overview', href: '/sub-account/reporting' },
  { id: 'sales', label: 'Sales', href: '/sub-account/reporting/sales' },
  { id: 'marketing', label: 'Marketing', href: '/sub-account/reporting/marketing' },
  { id: 'contacts', label: 'Contacts', href: '/sub-account/reporting/contacts' },
  { id: 'appointments', label: 'Appointments', href: '/sub-account/reporting/appointments' },
  { id: 'custom', label: 'Custom Reports', href: '/sub-account/reporting/custom' },
]

const DATE_RANGES = [
  { id: 'today', label: 'Today' },
  { id: '7d', label: 'Last 7 Days' },
  { id: '30d', label: 'Last 30 Days' },
  { id: '90d', label: 'Last 90 Days' },
  { id: 'ytd', label: 'Year to Date' },
]

const contactGrowth = [
  { month: 'Jul', added: 245, lost: 12 },
  { month: 'Aug', added: 312, lost: 18 },
  { month: 'Sep', added: 289, lost: 15 },
  { month: 'Oct', added: 356, lost: 22 },
  { month: 'Nov', added: 398, lost: 19 },
  { month: 'Dec', added: 425, lost: 14 },
]

const contactSources = [
  { source: 'Web Forms', count: 1245, percentage: 32 },
  { source: 'Imports', count: 892, percentage: 23 },
  { source: 'Manual Entry', count: 658, percentage: 17 },
  { source: 'API/Integration', count: 542, percentage: 14 },
  { source: 'Referral', count: 389, percentage: 10 },
  { source: 'Other', count: 156, percentage: 4 },
]

const tagStats = [
  { tag: 'Hot Lead', count: 342, color: 'bg-red-500' },
  { tag: 'Customer', count: 1256, color: 'bg-emerald-500' },
  { tag: 'VIP', count: 89, color: 'bg-amber-500' },
  { tag: 'Prospect', count: 567, color: 'bg-blue-500' },
  { tag: 'Inactive', count: 234, color: 'bg-slate-500' },
]

const recentActivity = [
  { type: 'added', count: 45, period: 'today' },
  { type: 'updated', count: 128, period: 'today' },
  { type: 'deleted', count: 3, period: 'today' },
  { type: 'tagged', count: 67, period: 'today' },
]

export default function ContactsReportingPage() {
  const [dateRange, setDateRange] = useState('30d')

  const totalContacts = 3882
  const activeContacts = 3245
  const newContactsThisMonth = contactGrowth[contactGrowth.length - 1].added
  const engagementRate = 68.5
  const maxGrowth = Math.max(...contactGrowth.map(c => c.added))

  return (
    <div className="space-y-6 pb-8">
      <div className="flex-none bg-white border-b border-slate-200 -mx-4 sm:-mx-6 lg:-mx-8 -mt-4 sm:-mt-6 lg:-mt-8 px-4 sm:px-6 lg:px-8 pt-4 sm:pt-6 lg:pt-8 pb-0 mb-8">
        <PageHeader
          title="Contacts Reports"
          description="Analyze contact growth, engagement, and segmentation"
          icon="fa-users"
          actions={
            <div className="flex items-center gap-2">
              <select value={dateRange} onChange={(e) => setDateRange(e.target.value)} className="px-4 py-2.5 border border-slate-300 rounded-xl text-sm font-medium focus:outline-none focus:ring-2 focus:ring-indigo-500 bg-white">
                {DATE_RANGES.map(range => <option key={range.id} value={range.id}>{range.label}</option>)}
              </select>
              <button className="px-5 py-2.5 bg-gradient-to-r from-indigo-500 to-purple-500 text-white rounded-xl font-medium hover:from-indigo-600 hover:to-purple-600 shadow-lg flex items-center gap-2">
                <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24"><path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 10v6m0 0l-3-3m3 3l3-3m2 8H7a2 2 0 01-2-2V5a2 2 0 012-2h5.586a1 1 0 01.707.293l5.414 5.414a1 1 0 01.293.707V19a2 2 0 01-2 2z" /></svg>
                Export
              </button>
            </div>
          }
        />
        <nav className="flex items-center gap-1 overflow-x-auto -mb-px">
          {REPORTING_TABS.map(tab => (
            <Link key={tab.id} href={tab.href} className={`px-4 py-3 text-sm font-medium border-b-2 transition-colors whitespace-nowrap ${tab.id === 'contacts' ? 'border-indigo-500 text-indigo-600' : 'border-transparent text-slate-500 hover:text-slate-700 hover:border-slate-300'}`}>
              {tab.label}
            </Link>
          ))}
        </nav>
      </div>

      {/* KPI Cards */}
      <div className="grid grid-cols-2 lg:grid-cols-4 gap-4">
        <div className="bg-gradient-to-br from-blue-500 to-indigo-500 rounded-2xl p-6 text-white">
          <p className="text-blue-100 text-sm font-medium">Total Contacts</p>
          <p className="text-3xl font-bold mt-2">{totalContacts.toLocaleString()}</p>
          <p className="text-blue-100 text-sm mt-1">+12.3% vs last period</p>
        </div>
        <div className="bg-white rounded-2xl shadow-sm border border-slate-200 p-6">
          <p className="text-slate-600 text-sm font-medium">Active Contacts</p>
          <p className="text-3xl font-bold text-slate-900 mt-2">{activeContacts.toLocaleString()}</p>
          <p className="text-emerald-600 text-sm mt-1">{((activeContacts / totalContacts) * 100).toFixed(1)}% of total</p>
        </div>
        <div className="bg-white rounded-2xl shadow-sm border border-slate-200 p-6">
          <p className="text-slate-600 text-sm font-medium">New This Month</p>
          <p className="text-3xl font-bold text-slate-900 mt-2">{newContactsThisMonth}</p>
          <p className="text-emerald-600 text-sm mt-1">+6.8% vs last month</p>
        </div>
        <div className="bg-white rounded-2xl shadow-sm border border-slate-200 p-6">
          <p className="text-slate-600 text-sm font-medium">Engagement Rate</p>
          <p className="text-3xl font-bold text-slate-900 mt-2">{engagementRate}%</p>
          <p className="text-emerald-600 text-sm mt-1">+3.2% vs last period</p>
        </div>
      </div>

      <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
        {/* Contact Growth Chart */}
        <div className="lg:col-span-2 bg-white rounded-2xl shadow-sm border border-slate-200 p-6">
          <h3 className="text-lg font-semibold text-slate-900 mb-6">Contact Growth</h3>
          <div className="h-64 flex items-end justify-between gap-4">
            {contactGrowth.map(month => (
              <div key={month.month} className="flex-1 flex flex-col items-center gap-2">
                <div className="w-full flex flex-col items-center gap-1 h-48">
                  <div className="w-full flex items-end justify-center h-full gap-1">
                    <div className="w-6 bg-gradient-to-t from-emerald-600 to-emerald-400 rounded-t" style={{ height: `${(month.added / maxGrowth) * 100}%` }} title={`Added: ${month.added}`}></div>
                    <div className="w-6 bg-gradient-to-t from-red-600 to-red-400 rounded-t" style={{ height: `${(month.lost / maxGrowth) * 100}%` }} title={`Lost: ${month.lost}`}></div>
                  </div>
                </div>
                <span className="text-xs text-slate-500 font-medium">{month.month}</span>
              </div>
            ))}
          </div>
          <div className="flex items-center justify-center gap-6 mt-4">
            <div className="flex items-center gap-2">
              <span className="w-3 h-3 bg-emerald-500 rounded-full"></span>
              <span className="text-sm text-slate-600">Added</span>
            </div>
            <div className="flex items-center gap-2">
              <span className="w-3 h-3 bg-red-500 rounded-full"></span>
              <span className="text-sm text-slate-600">Lost</span>
            </div>
          </div>
        </div>

        {/* Today's Activity */}
        <div className="bg-white rounded-2xl shadow-sm border border-slate-200 p-6">
          <h3 className="text-lg font-semibold text-slate-900 mb-4">Today's Activity</h3>
          <div className="space-y-4">
            {recentActivity.map(activity => (
              <div key={activity.type} className="flex items-center justify-between p-3 bg-slate-50 rounded-xl">
                <div className="flex items-center gap-3">
                  <div className={`w-10 h-10 rounded-lg flex items-center justify-center ${activity.type === 'added' ? 'bg-emerald-100 text-emerald-600' : activity.type === 'updated' ? 'bg-blue-100 text-blue-600' : activity.type === 'deleted' ? 'bg-red-100 text-red-600' : 'bg-amber-100 text-amber-600'}`}>
                    {activity.type === 'added' && <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24"><path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 4v16m8-8H4" /></svg>}
                    {activity.type === 'updated' && <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24"><path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 4v5h.582m15.356 2A8.001 8.001 0 004.582 9m0 0H9m11 11v-5h-.581m0 0a8.003 8.003 0 01-15.357-2m15.357 2H15" /></svg>}
                    {activity.type === 'deleted' && <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24"><path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 7l-.867 12.142A2 2 0 0116.138 21H7.862a2 2 0 01-1.995-1.858L5 7m5 4v6m4-6v6m1-10V4a1 1 0 00-1-1h-4a1 1 0 00-1 1v3M4 7h16" /></svg>}
                    {activity.type === 'tagged' && <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24"><path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M7 7h.01M7 3h5c.512 0 1.024.195 1.414.586l7 7a2 2 0 010 2.828l-7 7a2 2 0 01-2.828 0l-7-7A1.994 1.994 0 013 12V7a4 4 0 014-4z" /></svg>}
                  </div>
                  <span className="font-medium text-slate-900 capitalize">{activity.type}</span>
                </div>
                <span className="text-2xl font-bold text-slate-900">{activity.count}</span>
              </div>
            ))}
          </div>
        </div>
      </div>

      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        {/* Contact Sources */}
        <div className="bg-white rounded-2xl shadow-sm border border-slate-200 p-6">
          <h3 className="text-lg font-semibold text-slate-900 mb-4">Contact Sources</h3>
          <div className="space-y-4">
            {contactSources.map((source, index) => {
              const colors = ['bg-indigo-500', 'bg-blue-500', 'bg-purple-500', 'bg-pink-500', 'bg-amber-500', 'bg-slate-500']
              return (
                <div key={source.source}>
                  <div className="flex items-center justify-between mb-1">
                    <span className="text-sm font-medium text-slate-700">{source.source}</span>
                    <span className="text-sm text-slate-500">{source.count.toLocaleString()} ({source.percentage}%)</span>
                  </div>
                  <div className="h-3 bg-slate-100 rounded-full overflow-hidden">
                    <div className={`h-full ${colors[index]} rounded-full`} style={{ width: `${source.percentage}%` }}></div>
                  </div>
                </div>
              )
            })}
          </div>
        </div>

        {/* Tags Distribution */}
        <div className="bg-white rounded-2xl shadow-sm border border-slate-200 p-6">
          <h3 className="text-lg font-semibold text-slate-900 mb-4">Tags Distribution</h3>
          <div className="grid grid-cols-2 gap-4">
            {tagStats.map(tag => (
              <div key={tag.tag} className="flex items-center gap-3 p-3 bg-slate-50 rounded-xl">
                <span className={`w-3 h-3 ${tag.color} rounded-full`}></span>
                <div className="flex-1">
                  <p className="font-medium text-slate-900">{tag.tag}</p>
                  <p className="text-sm text-slate-500">{tag.count.toLocaleString()} contacts</p>
                </div>
              </div>
            ))}
          </div>
        </div>
      </div>
    </div>
  )
}
